.right {
    width: 50%;
}

.right h1 {
    padding-left: 0;
}

.right p {
    padding: 5px 30px 5px 0;
    line-height: 2;

}
.images {
    width: 100%;
    max-width: 80px;
    border-radius: 8px;
    margin-right: 12px;
    border: 2px solid transparent;
    cursor: pointer;
    margin-bottom: 18px;
}

.imageBorder {
    border: 2px solid #282c34;
}

.button {
    min-width: 80px;
    background-color: #282c34;
    border-radius: 8px;
    border: 0;
    text-align: center;
    margin-right: 12px;
    cursor: pointer;
    box-sizing: content-box;
    color: whitesmoke;
    padding: 12px 24px;
    font-weight: 600;
}

.activeButton {
    background-color: #282c34cc;
    
}

.buyButton {
    color: white;
    background-color: gray;
    font-size: 20px;
    border-radius: 8px;
    border: 0;
    padding: 12px 24px;
    margin-top: 30px; 
    margin-bottom: 30px;   
}

